import React, { useState, useEffect } from "react";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import axios from "axios";
import API_BASE_URL from "../config";
import "./EditOrder.css";

function EditOrder() {
  const { id } = useParams();
  const navigate = useNavigate();
  const location = useLocation();
  const orderNumber = location.state?.orderNumber; // Вот тут объявляем переменную

  

  const [userInfo, setUserInfo] = useState({ role: "", username: "" });
  const [orderData, setOrderData] = useState({
    full_name: "",
    phone_number: "",
    address: "",
    product: "",
    total_price: "",
    prepayment: "",
    status: "",
    assigned_to: "",
  });

  const [attachments, setAttachments] = useState([]);
  const [newFiles, setNewFiles] = useState([]);
  const [managers, setManagers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchOrder = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) return navigate("/auth");

        const response = await axios.get(`${API_BASE_URL}/api/orders/${id}/`, {
          headers: { Authorization: `Token ${token}` },
        });

        const data = response.data;
        setOrderData({
          full_name: data.full_name || "",
          phone_number: data.phone_number || "",
          address: data.address || "",
          product: data.product || "",
          total_price: data.total_price || "",
          prepayment: data.prepayment || "",
          status: data.status || "new",
          assigned_to: data.assigned_to || "",
        });

        setAttachments(data.attachments || []);
      } catch (err) {
        console.error("Ошибка загрузки заказа:", err);
        setError("Не удалось загрузить заказ для редактирования");
        navigate("/dashboard");
      } finally {
        setLoading(false);
      }
    };

    fetchOrder();
  }, [id, navigate]);

  useEffect(() => {
    const fetchManagers = async () => {
      try {
        const token = localStorage.getItem("token");
        const response = await axios.get(`${API_BASE_URL}/api/my_managers/`, {
          headers: { Authorization: `Token ${token}` },
        });
        setManagers(response.data);
      } catch (err) {
        console.error("Ошибка загрузки менеджеров", err);
      }
    };

    fetchManagers();
  }, []);

  useEffect(() => {
    const fetchUserInfo = async () => {
      const token = localStorage.getItem("token");
      try {
        const response = await axios.get(`${API_BASE_URL}/api/user_info/`, {
          headers: { Authorization: `Token ${token}` },
        });
        setUserInfo(response.data);
      } catch (error) {
        console.error("❌ Ошибка получения роли пользователя:", error);
      }
    };
    fetchUserInfo();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setOrderData((prev) => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e) => {
    const files = Array.from(e.target.files);
    setNewFiles(files);
  };

  const handleDeleteAttachment = async (attachmentId) => {
    try {
      const token = localStorage.getItem("token");
      await axios.delete(`${API_BASE_URL}/api/order-attachments/${attachmentId}/`, {
        headers: { Authorization: `Token ${token}` },
      });
      setAttachments((prev) => prev.filter((att) => att.id !== attachmentId));
    } catch (err) {
      console.error("Ошибка удаления вложения:", err);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token");
      const formData = new FormData();
      Object.keys(orderData).forEach((key) => {
        formData.append(key, orderData[key]);
      });
      newFiles.forEach((file) => {
        formData.append("attachments", file);
      });

      await axios.patch(`${API_BASE_URL}/api/orders/${id}/`, formData, {
        headers: {
          Authorization: `Token ${token}`,
          "Content-Type": "multipart/form-data",
        },
      });
      navigate(`/orders/${id}`);
    } catch (err) {
      console.error("Ошибка обновления заказа:", err);
    }
  };

  if (loading) return <p>Загрузка...</p>;
  if (error) return <p>{error}</p>;

  return (
    <div className="edit-order-container">
      <h2>Редактировать заказ #{orderNumber}</h2>
      <form className="edit-order-form" onSubmit={handleSubmit}>
        <input type="text" name="full_name" value={orderData.full_name} onChange={handleChange} placeholder="ФИО" />
        <input type="text" name="phone_number" value={orderData.phone_number} onChange={handleChange} placeholder="Телефон" />
        <input type="text" name="address" value={orderData.address} onChange={handleChange} placeholder="Адрес" />
        <input type="text" name="product" value={orderData.product} onChange={handleChange} placeholder="Изделие" />
        <input type="number" name="total_price" value={orderData.total_price} onChange={handleChange} placeholder="Цена" />
        <input type="number" name="prepayment" value={orderData.prepayment} onChange={handleChange} placeholder="Предоплата" />

        <select name="status" value={orderData.status} onChange={handleChange}>
          <option value="new">Новый</option>
          <option value="in_progress">В работе</option>
          <option value="completed">Завершён</option>
          <option value="cancelled">Отменён</option>
        </select>

        {userInfo.role === "owner" ? (
          <div className="form-group">
            <label>Назначить менеджера:</label>
            <select name="assigned_to" value={orderData.assigned_to || ""} onChange={handleChange}>
              <option value="">Не назначен</option>
              {managers.map((m) => (
                <option key={m.id} value={m.id}>{m.username}</option>
              ))}
            </select>
          </div>
        ) : (
          <div className="form-group">
            <label>Назначенный менеджер:</label>
            <p>👤 {managers.find((m) => String(m.id) === String(orderData.assigned_to))?.username || "Не назначен"}</p>
          </div>
        )}

        {attachments.length > 0 && (
          <ul>
            {attachments.map((att) => (
              <li key={att.id}>
                {decodeURIComponent(att.file.split("/").pop())}
                <button type="button" onClick={() => handleDeleteAttachment(att.id)}>Удалить</button>
              </li>
            ))}
          </ul>
        )}

        <input type="file" multiple onChange={handleFileChange} />

        <button type="submit">Сохранить</button>
        <button type="button" onClick={() => navigate(`/orders/${id}`)}>Отмена</button>
      </form>
    </div>
  );
}

export default EditOrder;
