import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./Dashboard.css"; 
import API_BASE_URL from "../config"; 







const getDaysPassed = (receivedDate) => {
  if (!receivedDate) return 0;
  const now = new Date();
  const received = new Date(receivedDate);
  const diffTime = now - received;
  return Math.floor(diffTime / (1000 * 60 * 60 * 24));
};

const getDayWord = (number) => {
  const n = Math.abs(number) % 100;
  const n1 = n % 10;

  if (n > 10 && n < 20) return "дней";
  if (n1 > 1 && n1 < 5) return "дня";
  if (n1 === 1) return "день";
  return "дней";
};

const getColorByDays = (days) => {
  if (days <= 7) return "green";
  if (days <= 20) return "#d2a100";
  if (days <= 30) return "brown";
  return "red";
};

const Dashboard = () => {
  const [orders, setOrders] = useState([]);
  const [filter, setFilter] = useState("active");
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  const filteredOrders = orders.filter((order) => {
    if (filter === "all") return true;
    if (filter === "active") return !["completed", "cancelled"].includes(order.status);
    if (filter === "completed") return ["completed", "cancelled"].includes(order.status);
    return true;
  });
  const sortedOrders = [...filteredOrders].sort((a, b) => {
    const dateA = new Date(a.received_date || 0);
    const dateB = new Date(b.received_date || 0);
    return dateA - dateB; // от самого старого к новому
  });

  const getGreeting = () => {
    const hours = new Date().getHours();
    if (hours >= 5 && hours < 9) return "Доброе утро!";
    if (hours >= 9 && hours < 18) return "Добрый день!";
    if (hours >= 18 && hours < 22) return "Добрый вечер!";
    return "Доброй ночи!";
  };

  useEffect(() => {
    const fetchOrders = async () => {
      try {
        const token = localStorage.getItem("token");

        if (!token) {
          console.warn("❌ Токен отсутствует! Перенаправление на вход...");
          navigate("/auth");
          return;
        }

        console.log("🔄 Отправляем запрос на заказы с токеном:", token);
        const response = await axios.get(`${API_BASE_URL}/api/orders/`, {
          headers: { Authorization: `Token ${token}` },
        });

        console.log("✅ Заказы загружены:", response.data);
        setOrders(response.data);
      } catch (error) {
        console.error("❌ Ошибка загрузки заказов:", error.response?.data || error);
        navigate("/auth");
      } finally {
        setLoading(false);
      }
    };

    fetchOrders();
  }, [navigate]);

  const handleLogout = () => {
    console.log("🚪 Выход пользователя");
    localStorage.removeItem("token");
    navigate("/auth");
  };

  const handleSettings = () => {
    console.log("⚙️ Переход в настройки");
    navigate("/settings");
  };

  const handleDeleteOrder = async (orderId) => {
    const confirmDelete = window.confirm("Вы уверены, что хотите удалить этот заказ?");
    if (!confirmDelete) return;

    try {
      const token = localStorage.getItem("token");
      
      console.log(`🗑 Удаляем заказ ${orderId}...`);
      await axios.delete(`${API_BASE_URL}/api/orders/${orderId}/`, {
        headers: { Authorization: `Token ${token}` },
      });

      console.log(`✅ Заказ ${orderId} успешно удалён`);
      setOrders((prevOrders) => prevOrders.filter((order) => order.id !== orderId));
    } catch (error) {
      console.error("❌ Ошибка при удалении заказа:", error.response?.data || error);
      alert("Ошибка при удалении заказа. Попробуйте снова.");
    }
    
  };

  

  return (
    <div className="dashboard-container">
      <h2>{getGreeting()}</h2>

      <div className="button-group">
        <button className="create-order-button" onClick={() => navigate("/create-order")}>
          Создать заказ
        </button>
        <button className="settings-button" onClick={handleSettings}>
          ⚙️ Настройки
        </button>
        <button className="logout-button" onClick={handleLogout}>
          🚪 Выйти
        </button>
      </div>

      <h3>Ваши заказы:</h3>
      <div className="filter-buttons">
  <button onClick={() => setFilter("active")} className={filter === "active" ? "active" : ""}>🟢 Активные</button>
  <button onClick={() => setFilter("all")} className={filter === "all" ? "active" : ""}>📦 Все</button>
  <button onClick={() => setFilter("completed")} className={filter === "completed" ? "active" : ""}>✅ Завершённые</button>
</div>
      {loading ? (
        <p>Загрузка заказов...</p>
      ) : orders.length > 0 ? (
        <ul className="order-list">
          {sortedOrders.map((order) => (
            <li 
              key={order.id} 
              className="order-item"
             >
              <p><strong>Номер заказа:</strong> {order.order_number || "–"}</p>
              <p><strong>ФИО:</strong> {order.full_name || "Не указано"}</p>
              <p><strong>Телефон:</strong> {order.phone_number || "Не указан"}</p>
              <p><strong>Адрес:</strong> {order.address || "Не указано"}</p>
              <p>
  <strong>Дата принятия заказа:</strong>{" "}
  {order.received_date ? (
    <>
      {new Date(order.received_date).toLocaleDateString()}
      {getDaysPassed(order.received_date) > 0 && (
  <span style={{ fontWeight: "500", color: getColorByDays(getDaysPassed(order.received_date)) }}>
    {" — ⏱️ прошло "}{getDaysPassed(order.received_date)} {getDayWord(getDaysPassed(order.received_date))}
  </span>
)}
    </>
  ) : (
    "Не указано"
  )}
</p>



              <div className="button-group">
                <button className="details-button" onClick={() => navigate(`/orders/${order.id}`)}>
                  Подробнее
                </button>
             
                <button className="delete-order-button" onClick={() => handleDeleteOrder(order.id)}>
                  Удалить
                </button>
              </div>
            </li>
          ))}
        </ul>
      ) : (
        <p>У вас пока нет созданных заказов.</p>
      )}
    </div>
  );
};

export default Dashboard;