import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./CreateOrder.css";
import API_BASE_URL from "../config";

const CreateOrder = () => {
  const [receivedDate, setReceivedDate] = useState("");
  const [orderNumber, setOrderNumber] = useState("");
  const [fullName, setFullName] = useState("");
  const [address, setAddress] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [product, setProduct] = useState("");
  const [manufacturingDays, setManufacturingDays] = useState("");
  const [price, setPrice] = useState("");
  const [prepayment, setPrepayment] = useState("");
  const [attachments, setAttachments] = useState([]);
  const [message, setMessage] = useState("");
  const [isSaving, setIsSaving] = useState(false);
  const [managers, setManagers] = useState([]);
  const [assignedTo, setAssignedTo] = useState("");
  const [role, setRole] = useState(""); // 🆕 Получим роль пользователя
  const [formDisabled, setFormDisabled] = useState(false);
  const navigate = useNavigate();


  const getDaysPassed = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffTime = now - date;
    return Math.floor(diffTime / (1000 * 60 * 60 * 24));
  };
  
  const getDaysColor = (days) => {
    if (days <= 7) return "green";       // до 7 дней — 🟢
    if (days <= 20) return "#d2a100";     // 8–20 — 🟠
    if (days <= 30) return "#8b0000";     // 21–30 — 🔴 бордовый
    return "red";                         // > 30 — 🔴
  };
  
  const getStatusEmoji = (days) => {
    if (days <= 7) return "🟢";
    if (days <= 20) return "🟠";
    if (days <= 30) return "🔴";
    return "🔥";
  };


  useEffect(() => {
    const fetchUserInfo = async () => {
      const token = localStorage.getItem("token");
      try {
        const response = await axios.get(`${API_BASE_URL}/api/user_info/`, {
          headers: { Authorization: `Token ${token}` },
        });
        setRole(response.data.role); // 🆕 Устанавливаем роль
      } catch (error) {
        console.error("❌ Ошибка при получении роли:", error);
      }
    };

    const fetchManagers = async () => {
      const token = localStorage.getItem("token");
      try {
        const response = await axios.get(`${API_BASE_URL}/api/my_managers/`, {
          headers: { Authorization: `Token ${token}` },
        });
        setManagers(response.data);
      } catch (error) {
        console.error("❌ Ошибка загрузки менеджеров:", error);
      }
    };

    fetchUserInfo();
    fetchManagers();
  }, []);

  const handleFileChange = (e) => {
    setAttachments(Array.from(e.target.files));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (formDisabled) return;

    setFormDisabled(true); // ⛔ блокируем форму
    setMessage("");

    const token = localStorage.getItem("token");
    if (!token) {
      setMessage("❌ Ошибка: Вы не авторизованы!");
      setIsSaving(false);
      return;
    }

    const formData = new FormData();
    if (orderNumber) formData.append("order_number", orderNumber);
    if (fullName) formData.append("full_name", fullName);
    if (address) formData.append("address", address);
    if (phoneNumber) formData.append("phone_number", phoneNumber);
    if (product) formData.append("product", product);
    if (manufacturingDays) formData.append("manufacturing_days", manufacturingDays);
    if (receivedDate) formData.append("received_date", receivedDate);
    if (price) formData.append("total_price", price);
    if (prepayment) formData.append("prepayment", prepayment);
    if (role === "Владелец" && assignedTo) formData.append("assigned_to", assignedTo); // 🛡️ Только если владелец

    attachments.forEach((file) => {
      formData.append("attachments", file);
    });

    console.log("📡 Отправка формы:", Object.fromEntries(formData));

    try {
      await axios.post(`${API_BASE_URL}/api/orders/`, formData, {
        headers: {
          Authorization: `Token ${token}`,
          "Content-Type": "multipart/form-data",
        },
      });

      setMessage("✅ Заказ успешно создан!");
      setTimeout(() => navigate("/dashboard"), 2000);
    } catch (error) {
      console.error("❌ Ошибка при создании заказа:", error.response?.data || error);
      setMessage("❌ Ошибка при создании заказа.");
    } finally {
      // разблокируем форму через 2 секунды
      setTimeout(() => setFormDisabled(false), 2000);
    }
  };

  return (
    <div className="create-order-container">
      <h2>Создание заказа</h2>
      {message && <p className="message">{message}</p>}
      <form onSubmit={handleSubmit} className={formDisabled ? "disabled-form" : ""}>
        <input type="number" placeholder="Номер заказа (необязательно)" value={orderNumber} onChange={(e) => setOrderNumber(e.target.value)} />
        <input type="text" placeholder="ФИО" value={fullName} onChange={(e) => setFullName(e.target.value)} required />
        <input type="text" placeholder="Адрес" value={address} onChange={(e) => setAddress(e.target.value)} required />
        <input type="tel" placeholder="Номер телефона" value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} required />
        <input type="text" placeholder="Изделие" value={product} onChange={(e) => setProduct(e.target.value)} />
        <input type="number" placeholder="Срок изготовления (дней)" value={manufacturingDays} onChange={(e) => setManufacturingDays(e.target.value)} />
        <input type="date" placeholder="Дата заказа" value={receivedDate} onChange={(e) => setReceivedDate(e.target.value)} />
        {receivedDate && (
  <p
    style={{
      marginTop: "5px",
      fontWeight: "500",
      color: getDaysColor(getDaysPassed(receivedDate)),
    }}
  >
    🗓️ Дата приёма: {new Date(receivedDate).toLocaleDateString()} — прошло {getDaysPassed(receivedDate)} дней {getStatusEmoji(getDaysPassed(receivedDate))}
  </p>
)}
        <input type="number" placeholder="Цена изделия" value={price} onChange={(e) => setPrice(e.target.value)} />
        <input type="number" placeholder="Предоплата" value={prepayment} onChange={(e) => setPrepayment(e.target.value)} />
        <input type="file" multiple onChange={handleFileChange} />

        {/* Только для владельца — выбор менеджера */}
        {role === "Владелец" && managers.length > 0 && (
          <div>
            <label>Назначить менеджера:</label>
            <select value={assignedTo} onChange={(e) => setAssignedTo(e.target.value)}>
              <option value="">Не назначен</option>
              {managers.map((manager) => (
                <option key={manager.id} value={manager.id}>
                  {manager.username}
                </option>
              ))}
            </select>
          </div>
        )}

<button type="submit" className="save-button" disabled={isSaving}>
  {isSaving ? "Сохраняем..." : "Сохранить"}
</button>
      </form>
      <button onClick={() => navigate("/dashboard")} className="back-button">Назад</button>
    </div>
  );
};

export default CreateOrder;
