// src/pages/ActivationPage.jsx

import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";

export default function ActivationPage() {
  const { uid, token } = useParams();
  const [status, setStatus] = useState("loading");
  const navigate = useNavigate();

  useEffect(() => {
    const activate = async () => {
      try {
        const response = await fetch(`${process.env.REACT_APP_API_BASE_URL}/api/activate/${uid}/${token}/`
        ,
          { method: "GET" }
        );

        if (response.ok) {
          setStatus("success");
          setTimeout(() => navigate("/auth"), 3000);
        } else {
          setStatus("error");
        }
      } catch (error) {
        setStatus("error");
      }
    };

    activate();
  }, [uid, token, navigate]);

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-4 text-center">
      {status === "loading" && <p>⏳ Активация аккаунта...</p>}
      {status === "success" && (
        <>
          <h1 className="text-2xl font-bold text-green-600">🎉 Аккаунт активирован!</h1>
          <p>Сейчас вы будете перенаправлены на вход...</p>
        </>
      )}
      {status === "error" && (
        <>
          <h1 className="text-2xl font-bold text-red-600">❌ Ошибка активации</h1>
          <p>Ссылка недействительна или устарела.</p>
        </>
      )}
    </div>
  );
}