import React, { useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import API_BASE_URL from '../config';
import './ContactForm.css'; // Если стили общие, можно использовать их

const ContactForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
    attachment: null,
  });
  const [responseMessage, setResponseMessage] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e) => {
    setFormData((prev) => ({ ...prev, attachment: e.target.files[0] }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setResponseMessage('');
    const data = new FormData();
    data.append('name', formData.name);
    data.append('email', formData.email);
    data.append('subject', formData.subject);
    data.append('message', formData.message);
    if (formData.attachment) {
      data.append('attachment', formData.attachment);
    }

    try {
      const res = await axios.post(`${API_BASE_URL}/api/contact/`, data, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      setResponseMessage(res.data.message);
    } catch (error) {
      setResponseMessage(
        error.response?.data?.error || 'Ошибка при отправке сообщения.'
      );
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <h2>Связаться с разработчиком</h2>
        <form onSubmit={handleSubmit} encType="multipart/form-data">
          <input
            type="text"
            name="name"
            placeholder="Ваше имя"
            value={formData.name}
            onChange={handleChange}
            required
          />
          <input
            type="email"
            name="email"
            placeholder="Ваш email"
            value={formData.email}
            onChange={handleChange}
            required
          />
          <input
            type="text"
            name="subject"
            placeholder="Тема сообщения"
            value={formData.subject}
            onChange={handleChange}
            required
          />
          <textarea
            name="message"
            placeholder="Ваше сообщение"
            value={formData.message}
            onChange={handleChange}
            required
          />
          <input
            type="file"
            name="attachment"
            onChange={handleFileChange}
          />
          <button type="submit" className="auth-button">Отправить</button>
        </form>
        {responseMessage && <p className="error-message">{responseMessage}</p>}
        <p className="back-to-settings">
          <Link to="/settings">Вернуться к настройкам</Link>
        </p>
      </div>
    </div>
  );
};

export default ContactForm;
