import { useState, useEffect } from "react";
import { useNavigate, Link } from "react-router-dom";
import axios from "axios";
import API_BASE_URL from "../config";
import "./Settings.css";


const Settings = () => {
  const [companyName, setCompanyName] = useState("");
  const [joinCode, setJoinCode] = useState("");
  const [userInfo, setUserInfo] = useState(null);
  const navigate = useNavigate();

  // Загружаем данные о пользователе при загрузке страницы
  useEffect(() => {
    const fetchUserInfo = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) {
          navigate("/auth");
          return;
        }
        const response = await axios.get(`${API_BASE_URL}/api/user_info/`, {
          headers: { Authorization: `Token ${token}` },
        });
        setUserInfo(response.data);
        console.log("✅ Данные пользователя загружены:", response.data);
      } catch (error) {
        console.error("❌ Ошибка загрузки данных пользователя:", error.response?.data || error);
        navigate("/auth");
      }
    };

    fetchUserInfo();
  }, [navigate]);

  // Копирование кода компании
  const copyCodeToClipboard = () => {
    if (userInfo?.organization_code) {
      navigator.clipboard.writeText(userInfo.organization_code);
      alert("Код скопирован!");
    } else {
      alert("Нет кода для копирования.");
    }
  };

  // Создание компании
const createCompany = async () => {
  try {
    const token = localStorage.getItem("token");
    const response = await axios.post(
      `${API_BASE_URL}/api/create_organization/`,
      { name: companyName },
      { headers: { Authorization: `Token ${token}` } }
    );
    
    alert(`Компания "${companyName}" создана! Код: ${response.data.code}`);

    await fetchUserInfo(); //обновление страницы после действия
    
    setUserInfo((prev) => ({
      ...prev,
      organization: companyName,
      organization_code: response.data.code,
    }));
    
    console.log("✅ Компания создана:", response.data);
  } catch (error) {
    console.error("❌ Ошибка создания компании:", error.response?.data || error);

    // Показываем конкретное сообщение от бэкенда, если оно есть
    if (error.response && error.response.data && error.response.data.error) {
      alert(error.response.data.error);
    } else {
      alert("Ошибка при создании компании. Попробуйте позже.");
    }
  }
};

  // Присоединение к компании
  const joinCompany = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await axios.post(
        `${API_BASE_URL}/api/join_organization/`,
        { code: joinCode },
        { headers: { Authorization: `Token ${token}` } }
      );
      alert(`Вы присоединились к компании: ${response.data.message}`);

      await fetchUserInfo();

      setUserInfo((prev) => ({
        ...prev,
        organization: response.data.message,
      }));
      console.log("✅ Присоединение к компании:", response.data);
    } catch (error) {
      console.error("❌ Ошибка при присоединении:", error.response?.data || error);
      alert("Ошибка при присоединении. Проверьте код.");
    }
  };

  // Выход из компании с условием:
  // - Если роль "Владелец", дополнительно спрашиваем подтверждение и вызываем delete_organization.
  // - Если не владелец, вызываем leave_organization.
  const leaveCompany = async () => {
    if (!window.confirm("Вы уверены, что хотите покинуть компанию?")) return;
  
    try {
      const token = localStorage.getItem("token");
      await axios.post(`${API_BASE_URL}/api/leave_organization/`, {}, {
        headers: { Authorization: `Token ${token}` },
      });
  
      alert("Вы покинули компанию");
  
      setUserInfo((prev) => ({
        ...prev,
        organization: null,
        organization_code: null,
        role: "individual",
      }));
      navigate("/dashboard");
    } catch (error) {
      alert("Ошибка при выходе из компании");
    }
  };

  const deleteOrganization = async () => {
    if (!window.confirm("Вы уверены, что хотите удалить организацию? Все участники будут удалены.")) return;
  
    try {
      const token = localStorage.getItem("token");
      await axios.post(`${API_BASE_URL}/api/delete_organization/`, {}, {
        headers: { Authorization: `Token ${token}` },
      });
  
      alert("Организация удалена");
  
      setUserInfo((prev) => ({
        ...prev,
        organization: null,
        organization_code: null,
        role: "individual",
      }));
      navigate("/dashboard");
    } catch (error) {
      alert("Ошибка при удалении организации");
    }
  };
      

  // Вынеси функцию fetchUserInfo наружу:
const fetchUserInfo = async () => {
  try {
    const token = localStorage.getItem("token");
    const response = await axios.get(`${API_BASE_URL}/api/user_info/`, {
      headers: { Authorization: `Token ${token}` },
    });
    setUserInfo(response.data);
    console.log("✅ Данные пользователя загружены:", response.data);
  } catch (error) {
    console.error("❌ Ошибка загрузки данных пользователя:", error.response?.data || error);
    navigate("/auth");
  }
};
 

  return (
    <div className="settings-container modern-style">
      <h2>⚙️ Настройки</h2>
      <Link to="/settings/contact">Связь с разработчиком</Link>
      {/*<pre>{JSON.stringify(userInfo, null, 2)}</pre>*/}

      {/* Данные о пользователе */}
      {userInfo && (
        <div className="user-info-card">
          <p>👤 {userInfo.username}</p>
          <p>
            🏢 {userInfo.organization || "Без компании"} | Код: {userInfo.organization_code || "-"}
          </p>
          {userInfo.organization_code && (
            <button className="copy-button" onClick={copyCodeToClipboard}>
              📋
            </button>
          )}
        </div>
      )}

      {/* Создание компании */}
      {userInfo && userInfo.role === "individual" &&(
      <div className="card-style action-buttons">
        <h3>🏢 Создать свою компанию</h3>
        <input 
          type="text" 
          placeholder="Название компании" 
          value={companyName} 
          onChange={(e) => setCompanyName(e.target.value)}
          className="full-width-input"
        />
        
        <button className="create-button" onClick={createCompany}>➕ Создать</button>
      </div>)}

      {/* Присоединение к компании */}
      {userInfo && userInfo.role === "individual" &&(
      <div className="card-style action-buttons">
        <h3>🔑 Присоединиться к компании</h3>
        <input 
          type="text" 
          placeholder="Введите код компании" 
          value={joinCode} 
          onChange={(e) => setJoinCode(e.target.value)}
          className="full-width-input"
        />
        <button className="join-button" onClick={joinCompany}>🔑 Войти</button>
      </div>)}

      {/* Выход из компании */}
      {userInfo && userInfo.role === "owner" && (
  <button className="leave-button card-style" onClick={deleteOrganization}>
    ❌ Удалить организацию
  </button>
)}

{userInfo && userInfo.role === "manager" && (
  <button className="leave-button card-style" onClick={leaveCompany}>
    ❌ Покинуть компанию
  </button>
)}

      {/* Кнопка перехода к заказам */}
      <button className="dashboard-button card-style" onClick={() => navigate("/dashboard")}>
        ⬅️ Заказы
      </button>
    </div>
  );
};



export default Settings;