import React, { useState } from 'react';
import { useParams, useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import API_BASE_URL from '../config';
import './Auth.css';

const PasswordResetConfirm = () => {
  const { uid, token } = useParams();
  const navigate = useNavigate();
  
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage('');
    
    if (newPassword !== confirmPassword) {
      setMessage('❌ Пароли не совпадают');
      return;
    }
    
    try {
      const response = await axios.post(`${API_BASE_URL}/api/password-reset-confirm/${uid}/${token}/`, {
        new_password: newPassword,
        confirm_password: confirmPassword,
      });
      setMessage(response.data.message || 'Пароль успешно сброшен');
      // После успешного сброса можно перенаправить пользователя на страницу входа
      setTimeout(() => navigate('/auth'), 3000);
    } catch (error) {
      setMessage(error.response?.data?.error || 'Ошибка при сбросе пароля.');
    }
  };

  return (
    <div className="password-reset-container">
      <div className="password-reset-card">
        <h2>Установка нового пароля</h2>
        <form onSubmit={handleSubmit}>
          <input
            type="password"
            placeholder="Новый пароль"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder="Подтвердите новый пароль"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
          />
          <button type="submit" className="auth-button">Сбросить пароль</button>
        </form>
        {message && <p className="error-message">{message}</p>}
        <p className="back-to-login">
          <Link to="/auth">Вернуться к входу</Link>
        </p>
      </div>
    </div>
  );
};

export default PasswordResetConfirm;
