import React, { useState,} from 'react';
import axios from 'axios';
import API_BASE_URL from '../config';
import './PasswordReset.css'; // Можно использовать тот же файл стилей, если классы общие
import { Link } from 'react-router-dom';

const PasswordReset = () => {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage('');
    try {
      const response = await axios.post(`${API_BASE_URL}/api/password-reset/`, { email });
      setMessage(response.data.message);
    } catch (error) {
      setMessage(error.response?.data?.error || 'Ошибка при отправке запроса.');
    }
  };

  return (
    <div className="password-reset-container">
      <div className="password-reset-card">
        <h2>Восстановление пароля</h2>
        <p>Введите ваш email, и мы отправим инструкции для сброса пароля.</p>
        <form onSubmit={handleSubmit}>
          <input
            type="email"
            placeholder="Введите ваш email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <button type="submit" className="auth-button">Отправить письмо</button>
        </form>
        {message && <p className="error-message">{message}</p>}
        <p className="back-to-login">
          <Link to="/auth">Вернуться к входу</Link>
        </p>
      </div>
    </div>
  );
};

export default PasswordReset;
